  }
  .review-input{
    width: 60%;
    font-size: 1.2rem;
  }
  .review-input textarea{
    font-size: 1.2rem;
  }
  .add-review-image {
    width: 40%;
    height: 500px;
  }

  .title-review-image p {
    font-size: 4rem;
    padding: 5px;
  }
  .title-add-review{
    padding: 20px 10px;
  }
  .button-review{
    margin-top: 30px;
  }
  .card-reviews{
    flex: 0 0 40%;
    margin: 10px 25px;
  }
}
@media screen and (min-width: 1200px) {
  #resto {
    align-items: center;
    flex-direction: column;
    display: flex;
  }
  .card-list {
    max-width: 1400px;
  }
  .card {
    width: 30%;
  }
  .card-button{
    font-size: 1.3rem;
  }
  .resto-title{
font-size: 2.4rem;
  }
  .card-reviews{
    flex: 0 0 30%;
  }
